import React from "react";
import {
  Pane,
  Card,
  Heading,
  Text,
  UnorderedList,
  ListItem,
  Strong,
  Paragraph
} from "evergreen-ui";

export default function Terms() {
  return (
    <Card
      width="100%"
      display="flex"
      flexDirection="column"
      padding={24}
      background="white"
      elevation={0}
      borderRadius={8}
    >
      <style>{`
        .terms-container {
          max-width: 800px;
          margin: 0 auto;
        }

        .section {
          margin-bottom: 32px;
        }

        .section-title {
          color: #234361;
          margin-bottom: 16px;
        }

        .highlight-box {
          background: rgba(16, 112, 202, 0.05);
          border-radius: 8px;
          padding: 16px;
          margin: 16px 0;
          border: 1px solid rgba(16, 112, 202, 0.1);
        }
      `}</style>

      <div className="terms-container">
        <Heading size={700} marginBottom={32} color="#234361">
          Terms of Service
        </Heading>

        <div className="section">
          <Heading size={500} className="section-title">
            Acceptance of Terms
          </Heading>
          <Paragraph size={400} color="muted">
            By using the Music Mod Creation Tool (MMCT), you agree to these terms of service. Please read them carefully.
          </Paragraph>
          
          <div className="highlight-box">
            <Strong color="#1070CA">Important: </Strong>
            <Text>
              Your use of this tool constitutes acceptance of these terms. If you do not agree with these terms, 
              please do not use the tool.
            </Text>
          </div>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Use License
          </Heading>
          <UnorderedList>
            <ListItem>
              <Strong>Free Use:</Strong> This tool is provided free of charge for personal and non-commercial use.
            </ListItem>
            <ListItem>
              <Strong>Mod Creation:</Strong> You may use this tool to create music mods for supported Paradox games.
            </ListItem>
            <ListItem>
              <Strong>Rights:</Strong> You retain all rights to the music files you upload and the mods you create.
            </ListItem>
            <ListItem>
              <Strong>Compliance:</Strong> You must comply with Paradox Interactive's modding policies.
            </ListItem>
          </UnorderedList>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Limitations and Responsibilities
          </Heading>
          <Paragraph size={400} color="muted">
            While using MMCT, you agree not to use the tool for any illegal purposes. You must have the necessary 
            rights or permissions for any music files you upload and use in your mods.
          </Paragraph>
          
          <div className="highlight-box">
            <Strong>Warning: </Strong>
            <Text>
              You are solely responsible for ensuring you have the proper rights and permissions for any music 
              files you use in your mods. Unauthorized use of copyrighted material may result in legal consequences.
            </Text>
          </div>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Disclaimer
          </Heading>
          <Paragraph size={400} color="muted">
            MMCT is provided "as is" without any warranties of any kind. We are not responsible for any damages 
            or losses resulting from the use of this tool. You acknowledge that you use this tool at your own risk.
          </Paragraph>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Changes to Terms
          </Heading>
          <Paragraph size={400} color="muted">
            We reserve the right to modify these terms at any time. Continued use of the tool after changes 
            constitutes acceptance of the new terms. We recommend reviewing these terms periodically.
          </Paragraph>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Contact
          </Heading>
          <Paragraph size={400} color="muted">
            For any questions regarding these terms, please contact us through our{" "}
            <a href="https://discord.gg/q6v9eFERss" style={{ color: "#1070CA" }}>Discord server</a>.
          </Paragraph>
        </div>

        <Text size={300} color="muted">
          Last updated: {new Date().toLocaleDateString()}
        </Text>
      </div>
    </Card>
  );
} 