import React from "react";
import {
  Pane,
  Card,
  Text,
  Heading,
  IconButton,
  ChevronDownIcon,
  ChevronRightIcon,
} from "evergreen-ui";
import { FAQ } from "../data/faq";

export default function Help() {
  const [expandedQuestions, setExpandedQuestions] = React.useState({});

  const toggleQuestion = (index, e) => {
    e.stopPropagation();
    setExpandedQuestions(prev => ({
      ...prev,
      [index]: !prev[index]
    }));
  };

  const faq = FAQ.map((v, i) => {
    const isExpanded = expandedQuestions[i];
    
    return (
      <Card 
        key={`${v.q}-${i}`}
        className="faq-item"
        marginBottom={8}
        background="tint1"
        borderRadius={8}
        padding={16}
      >
        <Pane 
          display="flex" 
          alignItems="center" 
          justifyContent="space-between"
          onClick={(e) => toggleQuestion(i, e)}
          cursor="pointer"
        >
          <Text size={400} color="#234361" fontWeight={500}>
            {v.q}
          </Text>
          <IconButton
            icon={isExpanded ? ChevronDownIcon : ChevronRightIcon}
            appearance="minimal"
            size="small"
            onClick={(e) => toggleQuestion(i, e)}
          />
        </Pane>
        <div className={`faq-answer ${isExpanded ? 'expanded' : ''}`}>
          <Text size={300} color="#66788A">
            {v.a}
          </Text>
        </div>
      </Card>
    );
  });

  return (
    <Pane height="100%" className="help-container">
      <style>{`
        .help-container {
          overflow-y: auto;
          padding-right: 16px;
        }

        .help-container::-webkit-scrollbar {
          width: 8px;
        }

        .help-container::-webkit-scrollbar-track {
          background: rgba(255, 255, 255, 0.1);
          border-radius: 4px;
        }

        .help-container::-webkit-scrollbar-thumb {
          background: rgba(16, 112, 202, 0.3);
          border-radius: 4px;
          transition: all 0.3s ease;
        }

        .help-container::-webkit-scrollbar-thumb:hover {
          background: rgba(16, 112, 202, 0.5);
        }

        .faq-item {
          transition: all 0.3s ease;
        }

        .faq-item:hover {
          transform: translateY(-1px);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
        }

        .faq-answer {
          max-height: 0;
          overflow: hidden;
          transition: all 0.3s ease;
          opacity: 0;
          margin-top: 0;
        }

        .faq-answer.expanded {
          max-height: none;
          opacity: 1;
          margin-top: 12px;
        }

        .section-title {
          color: #234361;
          margin-bottom: 16px;
        }

        .help-footer {
          text-align: center;
          padding: 16px;
          margin-top: 24px;
          background: rgba(255, 255, 255, 0.5);
          border-radius: 8px;
        }
      `}</style>

      <Heading size={500} className="section-title">
        Common Questions
      </Heading>

      <div className="faq-list">
        {faq}
      </div>

      <Card className="faq-item" marginBottom={8} background="tint1" borderRadius={8} padding={16}>
        <Text size={400} color="#234361" fontWeight={500}>
          Where can I learn more about music modding?
        </Text>
        <div className="faq-answer expanded">
          <Text size={300} color="#66788A">
            Check out the Paradox Wikis for detailed guides, or join our Discord community for help and tutorials.
          </Text>
        </div>
      </Card>

      <div className="help-footer">
        <Text color="muted" size={300}>
          Still have questions? Join our Discord community for support!
        </Text>
      </div>
    </Pane>
  );
}