import React from "react";
import {
  Pane,
  Card,
  Heading,
  Text,
  UnorderedList,
  ListItem,
  Strong,
  Paragraph
} from "evergreen-ui";

export default function Privacy() {
  return (
    <Card
      width="100%"
      display="flex"
      flexDirection="column"
      padding={24}
      background="white"
      elevation={0}
      borderRadius={8}
    >
      <style>{`
        .privacy-container {
          max-width: 800px;
          margin: 0 auto;
        }

        .section {
          margin-bottom: 32px;
        }

        .section-title {
          color: #234361;
          margin-bottom: 16px;
        }

        .highlight-box {
          background: rgba(16, 112, 202, 0.05);
          border-radius: 8px;
          padding: 16px;
          margin: 16px 0;
          border: 1px solid rgba(16, 112, 202, 0.1);
        }
      `}</style>

      <div className="privacy-container">
        <Heading size={700} marginBottom={32} color="#234361">
          Privacy Policy
        </Heading>

        <div className="section">
          <Heading size={500} className="section-title">
            Overview
          </Heading>
          <Paragraph size={400} color="muted">
            The Music Mod Creation Tool (MMCT) is committed to protecting your privacy. 
            This Privacy Policy explains our data practices.
          </Paragraph>
          
          <div className="highlight-box">
            <Strong color="#1070CA">Key Point: </Strong>
            <Text>
              MMCT operates entirely within your browser and does not collect, store, 
              or transmit any personal data or user information.
            </Text>
          </div>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Data Collection
          </Heading>
          <UnorderedList>
            <ListItem>
              <Strong>No Personal Information:</Strong> We do not collect, store, or process any personal information.
            </ListItem>
            <ListItem>
              <Strong>No Usage Data:</Strong> We do not track or monitor how you use the tool.
            </ListItem>
            <ListItem>
              <Strong>No Cookies:</Strong> We do not use cookies or similar tracking technologies.
            </ListItem>
            <ListItem>
              <Strong>No File Storage:</Strong> Your uploaded music files are processed entirely in your browser 
              and are not stored on any servers.
            </ListItem>
          </UnorderedList>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Browser-Only Operation
          </Heading>
          <Paragraph size={400} color="muted">
            All operations, including file processing and mod generation, occur locally in your web browser. 
            No data is transmitted to external servers. If you refresh the page, all current work will be lost 
            as we do not maintain any state or save any information.
          </Paragraph>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Legal Notice
          </Heading>
          <Paragraph size={400} color="muted">
            MMCT is provided "as is" without any warranties of any kind. While we aim to make the tool useful 
            and reliable, we are not responsible for any issues that may arise from its use. Users are 
            responsible for ensuring they have the necessary rights to use any music files they process 
            through the tool and must comply with Paradox Interactive's modding policies and any applicable 
            copyright laws.
          </Paragraph>
          
          <div className="highlight-box">
            <Strong>Important: </Strong>
            <Text>
              By using MMCT, you acknowledge that you are responsible for any content you create and distribute 
              using this tool. Always ensure you have the necessary rights to use and distribute any music 
              files you include in your mods.
            </Text>
          </div>
        </div>

        <div className="section">
          <Heading size={500} className="section-title">
            Contact
          </Heading>
          <Paragraph size={400} color="muted">
            If you have any questions about this Privacy Policy, you can contact us through our{" "}
            <a href="https://discord.gg/q6v9eFERss" style={{ color: "#1070CA" }}>Discord server</a>.
          </Paragraph>
        </div>

        <Text size={300} color="muted">
          Last updated: {new Date().toLocaleDateString()}
        </Text>
      </div>
    </Card>
  );
} 