import { decode as decodeJpeg } from 'jpeg-js';
import { loadImageFromBlob } from './image-loader';
import { convertToDDS } from './DDSWriter';

export async function vic3_builder(mod, files, gameFolder) {
  /*
  # MUSIC MOD CREATION TOOL (MMCT) for Victoria 3

  # This is a script that creates the required .txt file for your Victoria 3 Music Mod based on the .ogg files passed into it.
  
  # The script will skip over any file called "maintheme" as this only replaces the loading music and is not shown in-game by the audio engine.
  # You are strongly encouraged to hop into the generated files and check that everything has been created properly. In particular, you may wish to add to the conditions in the .txt file that affect the chances of the music playing.
  
  # This script is open-source and was created by community composer Runite Drill / Utopia for ease in publishing music mods.
  # Feel free to join their music modding community and ask any questions: https://discord.gg/SdQhfBM
  
  # For more info/guide on music modding, please visit the V3 wiki: tba
  */

  const musicFolder = gameFolder.folder("music");
  const localizationFolder = gameFolder.folder("localization").folder("english");
  const categoryFolder = musicFolder.folder("music_player_categories");
  
  // Create custom folder with spaces instead of underscores
  const customFolderName = mod.name.replace(/_/g, " ");
  const customMusicFolder = musicFolder.folder(customFolderName);

  //Create .txt file
  const text = [textHeader];
  const localizationEntries = [localizationHeader];
  const trackIds = [];

  // Add category name to localization first
  localizationEntries.push(` ${mod.filename}: "${mod.name}"`);
  
  // Process image if provided
  if (mod.image) {
    try {
      const originalImage = await loadImageFromBlob(mod.image);

      const canvas = document.createElement('canvas');
      canvas.width = 800;
      canvas.height = 450;
      const ctx = canvas.getContext('2d');
      
      // Create temporary canvas for the original image
      const tempCanvas = document.createElement('canvas');
      tempCanvas.width = originalImage.width;
      tempCanvas.height = originalImage.height;
      const tempCtx = tempCanvas.getContext('2d');
      tempCtx.putImageData(new ImageData(originalImage.data, originalImage.width, originalImage.height), 0, 0);
      
      // Smart resize to 800x450
      ctx.drawImage(tempCanvas, 0, 0, 800, 450);

      // Convert to JPEG then DDS
      const jpegBlob = await new Promise(resolve => canvas.toBlob(resolve, 'image/jpeg', 0.95));
      const jpegArrayBuffer = await jpegBlob.arrayBuffer();
      const rawImageData = decodeJpeg(new Uint8Array(jpegArrayBuffer), { useTArray: true });
      const ddsBuffer = convertToDDS(rawImageData);

      // Save the DDS file in the correct location
      gameFolder
        .folder('gfx')
        .folder('interface')
        .folder('illustrations')
        .folder('music_player')
        .file(`${mod.filename}.dds`, new Uint8Array(ddsBuffer));
    } catch (err) {
      console.error('Image processing failed:', err);
    }
  }

  files.forEach((f) => {
    if (f.name.split(".")[0] !== "maintheme") {
      const trackId = `${f.name.split(".")[0]}_MMCT`;
      text.push(textDef(f, customFolderName));
      localizationEntries.push(localizationDef(f));
      trackIds.push(trackId);
      customMusicFolder.file(f.name, f);
    } else {
      const mainThemeFolder = musicFolder.folder("main_themes");
      mainThemeFolder.file(`${mod.filename}_maintheme.txt`, textDef(f, customFolderName));
      mainThemeFolder.file(f.name, f);
    }
  });
  
  customMusicFolder.file(`${mod.filename}.txt`, text.join("\n"));
  localizationFolder.file(`${mod.filename}_l_english.yml`, localizationEntries.join("\n"));
  
  // Create music player categories file
  const categoryContent = generateCategoryFile(mod.filename, trackIds);
  categoryFolder.file(`${mod.filename}.txt`, categoryContent);
}

const textHeader = `#This file was automatically generated by the Music Mod Creation Tool (MMCT), created by Runite Drill / Utopia.
#Consider joining the music modding discord server, Utopia, if you have any questions: https://discord.gg/SdQhfBM.
#Use the Music Mod Creation Tool to generate your own music mods for all Paradox Interactive games: https://runite-drill.github.io/music-mod-creation-tool/
  
#Please confirm the output is as expected before testing in-game.

#In this file you can specify the triggers, factors and conditions in which the music will play in-game.
#'mood' music is background music.
#The 'pause_factor' is a user-defined measure of how "heavy" the music is. When a song is played, its weight is added to a global counter. When that global counter reaches 100 it resets and the game pauses all music for a short period of time. This is give the listener some respite from any chain of potentially oerhwelming music tracks.
#'can_be_interrupted' determines whether the music track can be overriden by other music, such as event-triggered music.
#'is_prioritized_mood' will give the music track a 100% chance of playing the first time it is able to be played. Thereafter, it will be played at random with the same weighting as other mood-type music tracks. "no" disables this behaviour and makes the music track play as normal.

#NOTE: this tool does not generate code for event-triggered music. If you want it, you will need to do this yourself... sorry!
`;

const localizationHeader = `l_english:`;

function textDef(file, folderName) {
  return `${file.name.split(".")[0]}_MMCT = {
    music = "file:/music/${folderName}/${file.name}"
    pause_factor = 25
    mood = yes
    can_be_interrupted = yes
    is_prioritized_mood = no
    #is_valid = { 
      #is_at_war = no #example modifier: music will not play when at war (is_at_war modifier tbc)
    #}
}\n`;
}

function localizationDef(file) {
  // Get the original name without extension and replace underscores with spaces
  const displayName = file.originalName ? 
    file.originalName.split(".")[0].replace(/_/g, " ") : 
    file.name.split(".")[0].replace(/_/g, " ");
    
  return ` ${file.name.split(".")[0]}_MMCT: "${displayName}"`;
}

function generateCategoryFile(modName, trackIds) {
  const categoryHeader = `# This file was automatically generated by the Music Mod Creation Tool (MMCT)
# It defines the music player category for ${modName}

`;

  const tracks = trackIds.map(id => `\t\t"${id}"`).join("\n");

  return `${categoryHeader}category = {
\tid = "${modName}_category"
\tname = "${modName}"
\ttracks = {
${tracks}
\t}
}`;
}
