import React from "react";
import {
  Pane,
  Text,
  Card,
  StatusIndicator,
  Strong,
  Heading,
  Badge,
} from "evergreen-ui";
import { games } from "../data/games";

export default function Instructions({ className }) {
  const gameList = games.map((game, i) => {
    return game.isSupported ? (
      <Badge
        key={i}
        color={game.color}
        marginRight={8}
        marginBottom={8}
        fontSize="14px"
        padding={8}
        borderRadius="12px"
        display="flex"
        alignItems="center"
      >
        <StatusIndicator color={game.color} marginRight={6} />
        {game.title}
      </Badge>
    ) : null;
  });

  return (
    <Pane>
      <style>{`
        .instruction-section {
          margin-bottom: 24px;
        }

        .instruction-text {
          font-size: 15px;
          line-height: 1.6;
          color: #234361;
        }

        .games-grid {
          display: flex;
          flex-wrap: wrap;
          gap: 8px;
          padding: 16px;
          background: rgba(255, 255, 255, 0.5);
          border-radius: 12px;
          margin: 16px 0;
        }

        .highlight {
          color: #1070CA;
          font-weight: 500;
        }
      `}</style>

      <div className="instruction-section">
        <Heading size={500} marginBottom={12}>Welcome to the Music Mod Creation Tool!</Heading>
        <Text className="instruction-text">
          This tool simplifies the process of adding music to your Paradox Interactive game mods. 
          Currently supported games include:
        </Text>
      </div>

      <div className="games-grid">
        {gameList}
      </div>

      <div className="instruction-section">
        <Heading size={400} marginBottom={8}>How to Use</Heading>
        <Text className="instruction-text">
          1. Enter your mod name in the input field below<br/>
          2. Select the game(s) you want to create music files for<br/>
          3. Upload your <Strong className="highlight">.ogg</Strong> music files<br/>
          4. Download the generated <Strong className="highlight">.zip</Strong> file<br/>
          5. Copy the contents into your mod folder
        </Text>
      </div>

      <div className="instruction-section">
        <Heading size={400} marginBottom={8}>Important Notes</Heading>
        <Text className="instruction-text">
           Make sure your audio files are in <Strong className="highlight">.ogg</Strong> format<br/>
           File names should be unique and descriptive<br/>
           The tool will create all necessary folders and configuration files
        </Text>
      </div>

      <Card 
        background="tint1" 
        padding={16} 
        borderRadius={8}
        display="flex"
        alignItems="center"
        justifyContent="center"
      >
        <Text className="instruction-text" textAlign="center">
          That's it! The tool handles all the complex file generation for you. 
        </Text>
      </Card>
    </Pane>
  );
}
