import React from "react";
import {
  Button,
  Card,
  Icon,
  Pane,
  Text,
  Heading,
  ChevronDownIcon,
  ChevronUpIcon,
  InfoSignIcon,
  LearningIcon,
  ManualIcon,
  BuildIcon,
  PredictiveAnalysisIcon,
  DocumentIcon,
} from "evergreen-ui";
import Title from "./Title";
import ModInput from "./ModInput";
import Instructions from "./Instructions";
import About from "./About";
import Help from "./Help";
import Privacy from "./Privacy";
import Terms from "./Terms";
import Footer from "./Footer";
import wallpaper from "../assets/wallpaper.png";

export default function Page() {
  const [isShowingDetails, setIsShowingDetails] = React.useState(false);
  const [activeSection, setActiveSection] = React.useState(null);

  const handleSectionClick = (section) => {
    setActiveSection(activeSection === section ? null : section);
  };

  const sections = [
    {
      id: 'instructions',
      title: 'Getting Started',
      icon: ManualIcon,
      description: 'Learn how to create your first music mod with our step-by-step guide.',
      component: Instructions,
      color: '#1070CA'
    },
    {
      id: 'about',
      title: 'About',
      icon: BuildIcon,
      description: 'Discover the features and capabilities of the Music Mod Creation Tool.',
      component: About,
      color: '#47B881'
    },
    {
      id: 'help',
      title: 'Frequently Asked Questions',
      icon: PredictiveAnalysisIcon,
      description: 'Find answers to common questions about music modding.',
      component: Help,
      color: '#D9822B'
    }
  ];

  const overlayComponents = {
    privacy: Privacy,
    terms: Terms
  };

  return (
    <Pane
      height="100vh"
      display="flex"
      alignItems="stretch"
      justifyContent="center"
      backgroundImage={`url(${wallpaper})`}
      backgroundSize="cover"
      backgroundPosition="center"
      backgroundRepeat="no-repeat"
      overflowY="auto"
      className="main-container"
    >
      <style>{`
        .main-container::before {
          content: '';
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(255, 255, 255, 0.1);
          backdrop-filter: blur(10px);
          -webkit-backdrop-filter: blur(10px);
          z-index: 0;
        }
        
        .content-container {
          position: relative;
          z-index: 1;
          max-width: 1200px;
          width: 100%;
          margin: 0 auto;
          padding: 2rem;
          height: fit-content;
          min-height: 100%;
        }

        .main-content-area {
          position: relative;
          min-height: 400px;
        }

        .overlay-section {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          z-index: 10;
          animation: fadeIn 0.3s ease;
        }

        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translateY(-10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        .glass-card {
          background: rgba(255, 255, 255, 0.7) !important;
          backdrop-filter: blur(10px);
          -webkit-backdrop-filter: blur(10px);
          border: 1px solid rgba(255, 255, 255, 0.2) !important;
          border-radius: 16px !important;
          box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1) !important;
          transition: all 0.3s ease;
          overflow: visible !important;
        }

        .glass-card:hover {
          transform: translateY(-2px);
          box-shadow: 0 12px 48px rgba(0, 0, 0, 0.15) !important;
        }

        .action-button {
          transition: all 0.3s ease;
        }

        .action-button:hover {
          transform: translateY(-2px);
        }

        .details-section {
          transition: all 0.5s ease;
          opacity: ${isShowingDetails ? 1 : 0};
          max-height: ${isShowingDetails ? '2000px' : '0'};
          overflow: hidden;
          margin-top: 32px;
        }

        .details-grid {
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
          gap: 24px;
          margin-bottom: 24px;
          position: relative;
        }

        .section-card {
          height: ${activeSection ? '500px' : '200px'};
          transition: all 0.5s ease;
          position: relative;
          overflow: hidden;
          cursor: pointer;
          padding: 24px;
          display: flex;
          flex-direction: column;
          opacity: 1;
          visibility: visible;
          grid-column: auto;
        }

        .section-card.active {
          grid-column: 1 / -1;
          cursor: default;
          z-index: 2;
        }

        .section-card.inactive {
          opacity: 0;
          visibility: hidden;
          position: absolute;
          pointer-events: none;
          transform: scale(0.9);
        }

        .section-preview {
          opacity: ${activeSection ? 0 : 1};
          transition: opacity 0.3s ease;
          height: ${activeSection ? '0' : '100%'};
          overflow: hidden;
          display: flex;
          flex-direction: column;
          gap: 16px;
        }

        .section-content {
          opacity: ${activeSection ? 1 : 0};
          transition: opacity 0.3s ease;
          overflow-y: auto;
          height: 100%;
          display: ${activeSection ? 'block' : 'none'};
        }

        .section-icon {
          width: 40px;
          height: 40px;
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          background: rgba(255, 255, 255, 0.3);
          margin-bottom: 16px;
        }

        .expand-button {
          width: 200px;
          height: 48px;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          background: rgba(255, 255, 255, 0.2);
          backdrop-filter: blur(10px);
          -webkit-backdrop-filter: blur(10px);
          border: 1px solid rgba(255, 255, 255, 0.3);
          border-radius: 24px;
          color: #234361;
          font-weight: 500;
          cursor: pointer;
          transition: all 0.3s ease;
        }

        .expand-button:hover {
          background: rgba(255, 255, 255, 0.3);
          transform: translateY(-2px);
        }

        .expand-button:active {
          transform: translateY(0);
        }
      `}</style>

      <Pane className="content-container">
        <Title />
        <Card height={32} />
        
        <div className="main-content-area">
          <ModInput />
          
          {activeSection && overlayComponents[activeSection] && (
            <div className="overlay-section">
              <Card
                width="100%"
                display="flex"
                flexDirection="column"
                justifyContent="center"
                padding={24}
                background="white"
                elevation={0}
                borderRadius={8}
                marginBottom={24}
              >
                {React.createElement(overlayComponents[activeSection])}
              </Card>
            </div>
          )}
        </div>

        <Card height={32} />
        
        <div className="details-section">
          <div className="details-grid">
            {sections.map(section => (
              <Card 
                key={section.id}
                className={`glass-card section-card ${
                  activeSection === section.id ? 'active' : 
                  activeSection && activeSection !== section.id ? 'inactive' : ''
                }`}
                onClick={() => handleSectionClick(section.id)}
              >
                <div className="section-preview">
                  <div className="section-icon">
                    <Icon icon={section.icon} size={24} color={section.color} />
                  </div>
                  <Heading size={600} color="#234361">
                    {section.title}
                  </Heading>
                  <Text color="#66788A" size={400}>
                    {section.description}
                  </Text>
                </div>
                <div className="section-content">
                  {section.component && <section.component />}
                </div>
              </Card>
            ))}
          </div>
        </div>

        <Pane display="flex" justifyContent="center" marginTop={32}>
          <Button 
            onClick={() => {
              setIsShowingDetails(!isShowingDetails);
              setActiveSection(null);
            }}
            className="expand-button"
          >
            <Icon
              icon={isShowingDetails ? ChevronUpIcon : ChevronDownIcon}
              size={16}
            />
            {isShowingDetails ? "Show Less" : "Learn More"}
            <Icon
              icon={isShowingDetails ? LearningIcon : InfoSignIcon}
              size={16}
            />
          </Button>
        </Pane>
        
        <Card height={32} />
        <Footer 
          onPrivacyClick={handleSectionClick} 
          onTermsClick={handleSectionClick}
        />
      </Pane>
    </Pane>
  );
}
